 intérpretes para facilitar el acceso a servicios de salud a personas pertenecientes a pueblos étnicos o con discapacidad auditiva (lenguaje de señas). En el nivel institucional, interactúa la ciudadanía con los servidores públicos y privados, fomentando la creación de las asociaciones de usuarios y, por ende, la gestión y pronta respuesta a las solicitudes de los mismos.")]),_vm._m(3),_c('div',{staticClass:"row justify-content-around align-items-center mb-5"},[_c('div',{staticClass:"col-lg-10 justify-content-md-center align-self-center"},[_c('div',{staticClass:"tarjeta color-primario mb-5 fond1 px-4 p-3 degra"},[_c('div',{staticClass:"row justify-content-around align-items-center"},[_vm._m(4),_c('div',{staticClass:"col"},[_c('div',{staticClass:"row justify-content-between align-items-center"},[_vm._m(5),_c('div',{staticClass:"col-sm-auto"},[_c('a',{staticClass:"boton bb",attrs:{"href":('https://www.funcionpublica.gov.co/web/eva/biblioteca-virtual/-/document_library/bGsp2IjUBdeu/view_file/34232755'),"target":"_blank"}},[_c('span',{staticClass:"px-4"},[_vm._v("Ver ")]),_c('i',{staticClass:"fas fa-link"})])])])])])])])]),_vm._m(6),_vm._m(7),_c('br'),_c('div',{staticClass:"h3 text-center mb-5"},[_vm._v("Participación ciudadana en salud")]),_vm._m(8)])],1)}
